<template>
  <div class="organ_wrap">
    <listSearch v-model="searchData" :searchArr="searchArr" @searchConfirm="goSearch"></listSearch>
    <div class="table_box">
      <div class="btn">
        <Button type="primary" @click="handleHear(true)">批量结案</Button>
      </div>
      <Table
       @on-select="selectOne"
      @on-select-cancel="selectCancel"
      @on-select-all="selectAll"
      @on-select-all-cancel="selectAllCancel"
      :columns="columns" 
      :data="tableData">
     
        <template slot-scope="{ row }" slot="state">
          <span style="color:#538FFF">{{caseStatus[row.state]}}</span>
        </template>
        <template slot-scope="{ row}" slot="action">
          <div class="count_calse">
            <a href="javascript:;"  @click="closeCase(row)" title="案件撤回">
               <img width="26" height="26" src="../../assets/images/cehui.png" alt />
              </a>
              <a href="javascript:;"  @click="backCase(2,row)" title="闭庭">
                <img width="26" height="26" src="../../assets/images/biting.png" alt />
              </a>
            
            <a href="javascript:;" @click="handleRecord(row)" title="记录">
                <img width="26" height="26" src="../../assets/images/jilu.png" alt />
              </a>
            <a href="javascript:;" title="重新生成撤案文书">
                <img width="26" height="26" src="../../assets/images/ceanwenshu.png" alt />
              </a>
            <a href="javascript:;"  title="重新生成结案文书">
                <img width="26" height="26" src="../../assets/images/jieanwenshu.png" alt />
              </a>
            <a href="javascript:;" @click="enterRoom(row)"  title="进入庭室">
                <img width="26" height="26" src="../../assets/images/tingshi.png" alt />
              </a>
            <a href="javascript:;" @click="closeRoom(row)"  title="关闭庭室">
                <img width="26" height="26" src="../../assets/images/closeTingshi.png" alt />
              </a>
            <a href="javascript:;"  @click="uploadList(row)" title="上传问题清单">
                <img width="26" height="26" src="../../assets/images/wentiqingdan.png" alt />
              </a>
            <a href="javascript:;" @click="look(row)" title="预览">
                <img width="26" height="26" src="../../assets/images/zhuting2.png" alt />
              </a>
          </div>
        </template>
      </Table>
      <Page
        style="margin-top:20px"
        :current="currentPage" 
       :total="total"
       :page-size="pageSize"
       :page-size-opts="pageOpts" 
       @on-change="changeCurrentPage"
       @on-page-size-change="changeSize"
       show-sizer show-elevator show-total 
      />
    </div>
    <!-- 批量结案 -->
    <Modal v-model="sureDialog" :footer-hide="true" title="操作">
      <Form ref="formValidate" :model="form" :rules="ruleValidate" :label-width="110">
        <FormItem label="文本类型" prop="type">
          <RadioGroup v-model="form.type">
            <Radio label="1">撤案决定书（组庭前）</Radio>
            <Radio label="2">撤案决定书（组庭后）</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="文本来源" prop="source">
          <RadioGroup v-model="form.source">
            <Radio label="1">结案模板</Radio>
            <Radio label="2">文件上传</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="选择文书模板" prop="template" v-if="form.source==1|| form.source==''">
          <Select v-model="form.template" placeholder="请选择">
            <Option value="beijing">New York</Option>
            <Option value="shanghai">London</Option>
            <Option value="shenzhen">Sydney</Option>
          </Select>
        </FormItem>
        <FormItem label="上传问题清单" prop="template" v-if="form.source==2">
           <Upload 
            action="//jsonplaceholder.typicode.com/posts/"
             :before-upload="handleUpload">
                <Button type="primary" size="small">浏览...</Button>
            </Upload>
        </FormItem>
      </Form>
      <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button style="margin-right:20px;" @click="sureDialog=false">取消</Button>
        <Button type="primary" @click="overCase">确定</Button>
      </div>
    </Modal>
    <!-- 撤案 -->
    <Modal v-model="backDialog" :footer-hide="true" title="撤案">
      <Form ref="backFormValidate" :model="backForm" :rules="backFormRule" :label-width="110">
        <FormItem label="结案文书" prop="type">
          <RadioGroup v-model="backForm.type">
            <Radio label="1">撤案决定书</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="文本来源" prop="source">
          <RadioGroup v-model="backForm.source">
            <Radio label="1">结案模板</Radio>
            <Radio label="2">文件上传</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="选择文书模板" prop="template" v-if="backForm.source==1|| backForm.source==''">
          <Select v-model="backForm.template" placeholder="请选择">
            <Option value="beijing">New York</Option>
            <Option value="shanghai">London</Option>
            <Option value="shenzhen">Sydney</Option>
          </Select>
        </FormItem>
        <FormItem label="上传问题清单"  v-if="backForm.source==2">
           <Upload 
            action="//jsonplaceholder.typicode.com/posts/"
            :before-upload="backHandleUpload">
                <Button type="primary" size="small">浏览...</Button>
            </Upload>
        </FormItem>
      </Form>
      <div style="border-top:1px solid #e8eaec;padding:12px 18px 12px 18px;">
        <Button style="margin-right:20px;" @click="backDialog=false">取消</Button>
        <Button type="primary" @click="backFormSubmit">确定</Button>
      </div>
    </Modal>
    <!-- 闭庭 -->
    <Modal v-model="closeDialog" :footer-hide="true" title="闭庭">
      <Form ref="closeFormValidate" :model="closeForm" :rules="closeFormRule" :label-width="110">
        <FormItem label="结案文书" prop="type">
          <RadioGroup v-model="closeForm.type">
            <Radio label="1">裁决书</Radio>
            <Radio label="2">调解书</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="文本来源" prop="source">
          <RadioGroup v-model="closeForm.source">
            <Radio label="1">结案模板</Radio>
            <Radio label="2">文件上传</Radio>
          </RadioGroup>
        </FormItem>
        <FormItem label="选择文书模板" prop="template" v-if="closeForm.source==1|| closeForm.source==''">
          <Select v-model="closeForm.template" placeholder="请选择">
            <Option value="beijing">New York</Option>
            <Option value="shanghai">London</Option>
            <Option value="shenzhen">Sydney</Option>
          </Select>
        </FormItem>
        <FormItem label="上传问题清单" v-if="closeForm.source==2">
           <Upload 
           action="//jsonplaceholder.typicode.com/posts/"
           :before-upload="closeHandleUpload">
                <Button type="primary" size="small">浏览...</Button>
            </Upload>
        </FormItem>
      </Form>
      <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button style="margin-right:20px;" @click="closeDialog=false">取消</Button>
        <Button type="primary" @click="closeFormSubmit">确定</Button>
      </div>
    </Modal>
    <!-- 上传问题清单 -->
      <Modal v-model="problemDialog" :footer-hide="true" title="问题清单">
      <Form ref="problemFormValidate" :model="problemForm" :rules="problemFormRule" :label-width="110">
        <FormItem label="问题清单名称" prop="name">
          <Input v-model="problemForm.name" />
        </FormItem>
        <FormItem label="问题描述" prop="desc">
          <Input  v-model="problemForm.desc" :maxlength="100" show-word-limit type="textarea" placeholder="Enter something..." style="width: 200px" />
        </FormItem>
        <FormItem label="上传问题清单" prop="file">
           <div style="display:flex">
             <Upload action=""
            :before-upload="problemHandleUpload">
                <Button type="primary" size="small">浏览...</Button>
            </Upload>
            <div style="margin-left:10px;height: 40px;">{{problemForm.file? problemForm.file.name: '未选择文件'}}</div>
           </div>
            <p>上传类型只支持xls、xlsx、doc、docx、pdf</p>
        </FormItem>
      </Form>
      <div style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button style="margin-right:20px;" @click="problemDialog=false">取消</Button>
        <Button type="primary" @click="problemFormSubmit">确定</Button>
      </div>
    </Modal>
    <!-- 记录 -->
    <Modal v-model="record" :footer-hide="true" title="记录" ok-text="记录">
      <Form ref="recordFormValidate" :model="recordForm" :rules="recordRuleValidate" :label-width="0">
        <div style="margin-bottom:10px">请输入记录：</div>
        <FormItem label prop="text">
          <Input
            v-model="recordForm.text"
            :maxlength="100"
            :disabled="isDisab"
            show-word-limit
            type="textarea"
            placeholder="请输入记录"
            style="width:100%"
          />
        </FormItem>
      </Form>
      <div v-if="!isDisab" style="border-top: 1px solid rgb(232, 234, 236);padding: 12px 18px;text-align: center;">
        <Button style="margin-right:20px;" @click="handleCancel">取消</Button>
        <Button type="primary" @click="recordSubmit">确定</Button>
      </div>
    </Modal>
  </div>
</template>
<script>
import listSearch from "@/components/list-search/search.vue";
import { listMixin } from "./mixin.js";
import { mapActions } from "vuex";
let columns = [
  {
    type: "selection",
    width: 100,
    align: "center"
  },
  {
    title: "案号",
     key: "code",
    align: "center"
  },
  {
    title: "案件类型",
    key: 'caseTypeName',
    align: "center"
  },
  {
    title: "申请人",
    key: 'applicantName',
    align: "center"
  },
  {
    title: "申请人仲裁员",
    key: "applicantArbName",
    align: "center"
  },
  {
    title: "被申请人",
    key: "respondentName",
    align: "center"
  },
  {
    title: "被申请人仲裁员",
     key: "respondentArbName",
    align: "center"
  },
  {
    title: "案件状态",
    key: "state",
    slot:"state",
    align: "center"
  },
  {
    title: "原因",
    key: "8"
  },
  {
    title: "操作",
    slot: "action",
    width: 460,
    align: "center"
  }
];
export default {
  components: {
    listSearch
  },
  mixins: [listMixin],
  data() {
    return {
      searchData: {
        number: "",
        person: "",
        backPerson: "",
        name: "",
        type: ""
      },
      searchArr: [
        {
          type: "input",
          value: "number",
          label: "案号",
          placeholder: "请输入案号"
        },
        {
          type: "input",
          value: "person",
          label: "申请人",
          placeholder: "请输入申请人"
        },
        {
          type: "input",
          value: "backPerson",
          label: "被申请人",
          placeholder: "请输入被申请人"
        },
        {
          type: "select",
          value: "name",
          label: "注册名称",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ],
          placeholder: "请选择"
        },
        {
          type: "select",
          value: "type",
          label: "案件类型",
          arr: [
            { label: "全部", value: 0 },
            { label: "通过", value: 1 },
            { label: "未通过", value: 2 }
          ],
          placeholder: "请选择"
        }
      ],
      columns: columns,
      selectedData: [],
      hearType: false, // 审理方式 单个 批量
      sureDialog: false, // 结案
      backDialog: false, // 撤案
      closeDialog: false, // 闭庭、
      problemDialog: false, //问题清单
      //   结案
      form: {
        type: "",
        source: "",
        template: "",
        file: ""
      },
      ruleValidate: {
        type: [{ required: true, message: "请选择类型", trigger: "change" }],
        source: [{ required: true, message: "请选择类型", trigger: "change" }],
        template: [{ required: true, message: "请选择模板", trigger: "change" }]
      },
      //   撤案
      backForm: {
        type: "",
        source: "",
        template: ""
      },
      backFormRule: {
        type: [{ required: true, message: "请选择类型", trigger: "change" }],
        source: [{ required: true, message: "请选择类型", trigger: "change" }],
        template: [{ required: true, message: "请选择模板", trigger: "change" }]
      },
      //  闭庭
      closeForm: {
        type: "",
        source: "",
        template: ""
      },
      closeFormRule: {
        type: [{ required: true, message: "请选择类型", trigger: "change" }],
        source: [{ required: true, message: "请选择类型", trigger: "change" }],
        template: [{ required: true, message: "请选择模板", trigger: "change" }]
      },
      //   问题清单
      problemForm: {
        name: "",
        desc: "",
        file: ""
      },
      problemFormRule: {
        name: [{ required: true, message: "不能为空", trigger: "change" }],
        desc: [{ required: true, message: "不能为空", trigger: "change" }],
        file: [
          {
            required: true,
            type: "object",
            message: "请上传文件",
            trigger: "change"
          }
        ]
      },
      defaultList: [], // 文件上传
      record:false, // 记录
      recordForm:{
        text:""
      },
      recordRuleValidate:{
        text:[{ required: true, message: "不能为空", trigger: "change" }]
      },
      isDisab:false,
      rowId:'',

    };
  },
  methods: {
    ...mapActions({
      getRequsetList: "createCase/getRequsetList",
      enterCourtRoom:"organizePeriod/enterCourtRoom",
      getCourtRecord:"organizePeriod/getCourtRecord",
      reserveMeeting:"organizePeriod/reserveMeeting",
      uploadReason:"organizePeriod/uploadReason",
      closeMeetingRoom:"organizePeriod/closeMeetingRoom",
      recordCount:"organizePeriod/recordCount", // 记录
    }),
    goSearch() {},
    //   value true 批量审理 false单个审理
    handleHear(value, row) {
      this.hearType = value;
      if (value) {
        if (this.selectedData.length === 0) {
          this.$Message.warning("请至少选择一条案件信息");
          return;
        }
        this.sureDialog = true;
      } else {
        this.sureDialog = true;
      }
    },
    // 结案
    overCase() {
      this.$refs["formValidate"].validate(valid => {
        console.log(this.form);
        if (valid) {
          this.$Message.success("Success!");
          // this.isShowDate=false
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    // 撤案
    backFormSubmit() {
      this.$refs["backFormValidate"].validate(valid => {
        console.log(this.backForm);
        if (valid) {
          this.$Message.success("Success!");
          // this.isShowDate=false
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    // 闭庭
    closeFormSubmit() {
      this.$refs["closeFormValidate"].validate(valid => {
        console.log(this.closeForm, "闭庭");
        if (valid) {
          this.$Message.success("Success!");
          // this.isShowDate=false
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    problemFormSubmit() {
      this.$refs["problemFormValidate"].validate(valid => {
        console.log(this.problemForm, "问题清单");

        if (valid) {
          let params = {
            entity_caseId:"",
            name:'',
            state:'',
            memo:'',
            fileids:''
          }
          // this.uploadReason()
          this.$Message.success("Success!");
          // this.isShowDate=false
        } else {
          this.$Message.error("Fail!");
        }
      });
    },
    // 撤案
    backCase(row, index) {
      console.log(row, index);
      this.backDialog = true;
    },
    // 闭庭
    closeCase(row) {
      this.closeDialog = true;
      
     
    },
    handleRecord(row){
      this.rowId=row.id
      this.recordForm.text=''
      this.record=true
      this.isDisab=false
      this.$nextTick(()=>{
        this.$refs["recordFormValidate"].resetFields()
      })
    },
   async recordSubmit(){
       this.$refs["recordFormValidate"].validate(async valid => {
        console.log(this.recordForm);
        
        if (valid) {
               let params= {   
                  caseId:this.rowId,//案件id
                  actionIdentifer:"MEETING_RECORD", //操作标识
                  content:this.recordForm.text
                  }
                console.log(params, '驳回参数')
                
          const result = await  this.recordCount(params);
             console.log(result);
            if (result.data.flag) {
                 this.$Message.success("操作成功");
                  //  重置
                  this.handleCancel();
                  this.getList()
                  
                } else {
                  this.$Message.error(result.data.message);
                }
          // this.isShowDate=false
        } else {
          // this.$Message.error("Fail!");
        }
      });
    },
    handleCancel(){
      this.record=false
      this.recordForm.text=''
      this.rowId=''
    },
    // 关闭庭室
    async closeRoom(row) {
      console.log(row);
      this.$Modal.confirm({
        title: "关闭庭室",
        content:
          '<div style="color:#3B4453;text-align:center;font-size:18px">确定要关闭庭室吗？</div>',
        onOk: async () => {
          let params={   
                     caseId:row.id,//案件id
                    actionIdentifer:"MEETING_CLOSE", //操作标识
                    }
            const result = await this.closeMeetingRoom(params)
            if (result.data.flag) {
            this.$Message.success("操作成功");
            this.getList()
          } else {
            this.$Message.error(result.data.message);
          }

        },
        onCancel: () => {
          this.$Message.info("Clicked cancel");
        }
      });
    },
    // 进入庭室
    async enterRoom(row){
      let params={   
            caseId:JSON.stringify(new Array(row.id)),//案件id
            actionIdentifer:" MEETING_OPEN", //操作标识
        }
      const result = await this.enterCourtRoom(params)
      if (result.data.flag) {
            // this.$Message.success("操作成功");
            var a = document.createElement('a');
              a.setAttribute('href', result.data.data);
              a.setAttribute('target', '_blank');
              document.body.appendChild(a);
              a.click();
           
          } else {
            this.$Message.error(result.data.message);
          }
    },
    // 预览
    async look(row){
      let params = {   
        caseId:row.id,//案件id
        actionIdentifer:"MEETING_RECORD", //操作标识
      }
      const result = await this.getCourtRecord(params)
       if (result.data.flag) {
         this.isDisab=true;
         this.record=true
            this.recordForm.text=result.data.data
          } else {
            this.$Message.error(result.data.message);
          }
    },
    uploadList(row) {
      console.log(row);
      this.problemDialog = true;
    },
    // 批量结案上传
    handleUpload(file) {
      console.log(file);
      this.form.file = file;
      // console.log(this.form)
      return false;
    },
    // 撤案
    backHandleUpload(file) {
      console.log(file);
      this.backForm.file = file;
      return false;
    },
    // 闭庭
    closeHandleUpload(file) {
      console.log(file);
      this.closeForm.file = file;
      return false;
    },
    // 上传问题清单
    problemHandleUpload(file) {
      // xls、xlsx、doc、docx、pdf
      const strRegex = "(.xlsx|.xls|.doc|.docx|.pdf)$";
      const reg = new RegExp(strRegex);
      if (reg.test(file.name.toLowerCase())) {
        this.problemForm.file = file;
      } else {
        this.$Message.error("文件格式错误！");
      }
      console.log(this.problemForm);
      this.$refs["problemFormValidate"].validate(); // 清除校验
      return false;
    },
    async getList() {
      let params = {
        menuIdentifer: "GROUP_SUCCESS_LIST", //菜单标识
        pageIndex: this.currentPage - 1,
        pageSize: this.pageSize
      };
      let filter = {
        //筛选条件
        applicantName: "", //申请人关键字
        respondentName: "", //被申请人关键字
        userId: "", //注册名称的用户id
        caseTypeId: "", //案件类型id
        createTimeBegin: "", //提交的开始时间
        createTimeEnd: "" //提交的结束时间
      };

      params = { ...params, filter: JSON.stringify({}) };
      // console.log("参数", params);
      const res = await this.getRequsetList(params);
      if (res.data.flag) {
        this.tableData = res.data.data.dataList;
        this.total = res.data.data.totalCount;
        this.selectedData = []; // 重置
      }
    }
  }
};
</script>
<style lang="less" scoped>
/deep/.ivu-table th {
  background-color: #3296fa;
}
/deep/thead .ivu-table-cell-with-selection::after {
  content: "全选";
  position: relative;
  left: 5px;
}
.organ_wrap {
  .table_box {
    background-color: #fff;
    margin-top: 30px;
    padding: 20px;
    .btn {
      text-align: right;
      margin-bottom: 20px;
    }
  }
  .count_calse {
    a {
      margin-right: 10px;
    }
  }
}
</style>
